    <html><head><meta http-equiv="Content-Type" content="text/html; charset=UTF-8"><meta http-equiv="Content-Style-Type" content="text/css"><meta name="generator" content="Aspose.Words for .NET 17.1.0.0"><title></title></head><body><div><p style="margin-top:0pt; margin-bottom:0pt; text-align:center; line-height:150%; widows:0; orphans:0; font-size:22pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">用户协议</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">&nbsp;</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">为使用</span><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">产品和服务，用户（下称“您”）应当阅读并遵守《</span><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">用户服务协议》（以下简称“本协议”）、《隐私政策》等相关协议和业务规则。请您务必审慎阅读、充分理解各条款内容，</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">特别是限制或免除责任条款，以及开通或使用某项服务的单独协议。在您开始阅读并决定是否接受本协议之前，</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">特别提醒您应留意本协议中“加粗形式”的条款内容。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">除非您已阅读并接受本协议所有条款，否则您无权使用本服务。在您选择接受本协议所有条款后，方可进行对本服务的登录、查看、下载/缓存、上传/发布信息、使用等行为。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">若您是18周岁以下的未成年人，在使用</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">服务前，请务必在您父母或法定监护人的陪同下阅读本协议，并特别注意未成年人使用条款。请您务必取得您父母或法定监护人的同意，并在监护人在场的情况下使用本协议所述的服务。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">1、 协议更新</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">有权随时对本协议进行修改，并且一旦发生协议条款的变动，</span><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">将在相关页面上提示修改的内容。如果您不能接受本协议修改或变更后的内容，您可以停止使用</span><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">。如果您继续使用</span><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">，则表明您完全接受并愿意遵守修改或变更后的本协议。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">&nbsp;</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">2、 服务形式</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">为您直接上载、传播内容提供平台和信息存储空间服务，您可以通过上传入口自行上传内容；</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">有权通过不同的渠道，为用户上载、传播的内容提供管理、传播服务；且</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">并不对上述内容承担任何的法律责任。</span><span style="font-family:&#39;宋体&#39;">除非另有证明，否则</span><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">储存在其服务器上的数据是您使用</span><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">软件或服务的唯一有效证据。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">&nbsp;</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">3、 用户规范：</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">用户在本APP的任何行为必须遵循：</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">1)</span><span style="font-family:&#39;宋体&#39;">传输资料时必须符合中国有关法规。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">2)</span><span style="font-family:&#39;宋体&#39;">使用信息服务不作非法用途和不道德行为。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">3)</span><span style="font-family:&#39;宋体&#39;">不干扰或混乱网络服务。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">4)</span><span style="font-family:&#39;宋体&#39;">遵守所有使用服务的网络协议、规定、程序和惯例。用户的行为准则是以因特网法规，政策、程序和惯例为根据的。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">您可以向</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">上传视频、音频、多媒体资料等内容，但您应保证：</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">1)</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">您享有完全权利或和已经取得完整授权，可将这些内容上传至</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">；</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">2)</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">您确认这些内容，并不侵犯任何第三方的肖像权或隐私权，不会贬损任何第三方的人格或商业声誉，不侵犯任何第三方的知识产权或其他合法权利；</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">3)</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">您上传这些内容并不违反您与第三方签订的任何对您有约束力的法律文件的规</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">定</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">；</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">4)</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">这些内容不含有暴力、色情、恐怖等非法内容、不含有违反法律、公序良俗等内容；</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">铃声魔盒</span><span style="font-family:&#39;宋体&#39;">一旦发现上述内容，有权随时删除您所上传的内容，并保留不通知您的权利。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">您同意：您向</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">上传内容，即表示您授权</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">及其关联公司以非专有的方式使用，</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">有权自行或转授权他人对前述内容进行复制、编辑、修改、展示及网络传播；前述授权是免费的、无限期的。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">&nbsp;</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">4、 未成年保护</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">如果您是未成年，您在使用本服务时应特别注意以下事项：</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">填写个人信息时，加强个人保护意识，并在取得监护人的同意以及在监护人的指导下进行填写提供，以免不良分子对个人造成骚扰和伤害；</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">在您父母、法定监护人的指导下，学习正确使用网络，养成良好上网习惯；</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">为更好地保护未成年人隐私权益，您在发布包含未成年人素材的内容前应审慎考虑，一经发布，即视为您已获得相关权利人同意在</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">软件及服务中展示未成年人的肖像、声音、姓名、年龄等信息，且同意</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">依据本协议使用、处理与未成年人相关的内容。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">父母或法定监护人特别提示：被监护人（即未成年人）使用</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">的，作为监护人应指导并监督被监护人使用行为，如被监护人登录、使用</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">，</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">铃声魔盒</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">有权认为其已取得您的同意。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">&nbsp;</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">5</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">、 拒绝提供担保</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">用户明确同意使用本公司服务，由用户个人承担风险。本APP不担保服务一定满足用户的要求，也不担保服务不会中断，对服务的及时性、安全性、出错发生都不作担保。用户理解并接受：任何通过服务取得的信息资料的可靠性有用性取决于用户自己的判断，用户自己承担所有风险和责任。</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;">&nbsp;</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5pt"><span style="font-family:&#39;宋体&#39;; font-weight:bold">6</span><span style="font-family:&#39;宋体&#39;; font-weight:bold">、 免责声明</span></p><p style="margin-top:0pt; margin-bottom:0pt; text-align:justify; line-height:150%; widows:0; orphans:0; font-size:10.5p